import { createWebHistory, createRouter } from "vue-router";
import Bundle from "../Bundle.vue";
import Gallery from "../components/Gallery.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Bundle,
  },
  {
    path: "/gallery",
    name: "Gallery",
    component: Gallery,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

